.movie-container {
    padding: 5%;
    margin-left: 20%;
    margin-right: 20%;
    background-color: #22262f;
    display: flex;
    flex-direction: column;
}

.movie-name-row {
    flex-direction: row;
    padding-bottom: 3%;
    display: flex;
}

.movie-title {
    font-size: 3rem;
    line-height: 3.125rem;
    flex: 5;
    text-align: left;
}

.movie-name-row-spacer {
    flex: 2;
}

.rr-holder {
    flex: 3;
    display: flex;
flex-direction: column;
text-align: left;
font-size: 1rem;
}

.movie-poster-row{
    display: flex;
    flex-direction: row;
}

.movie-poster-container{
    flex: 2;
}

.movie-trailer-container{
    flex: 8;
}

.movie-description-container{
    display: flex;
    flex-direction: column;
    font-size: small;
}

.movie-description-entry{
    display: flex;
    flex-direction: row;
    margin-top: 3%;
border-top: 0.1px solid;
padding-top: 2%;
}

.movie-description-entry span{
    flex: 1;
    text-align: left;
}

.movie-description-entry-value{
    flex:3 !important;
}

.arrow {
    border: solid rgb(255, 255, 255);
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
  }

  
  .left {
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
  }
  

  .movie-back-link{
    text-decoration: none;
    color: white;
    margin: 5px;
    max-width: 40%;
    font-size: small;
    }

.back-button-row{
    text-align: left;
    padding-bottom: 5%;
}